import isEmpty from 'lodash/isEmpty';
import { style } from './constants';
import get from 'lodash/get';
import { overflowActionsTooltip, TWO_LABEL_VALUE_BREAKPOINT, THREE_LABEL_VALUE_BREAKPOINT, FOUR_LABEL_VALUE_BREAKPOINT, MEDIUM, SMALL, CARD } from './constants';

export const getSecondaryInformation = (data) => data.map((content) => {
	let item = Object.values(content);
	if (item[0] && item[1]) {
		return [{
			'label': `${item[0]}`,
			'value': `${item[1]}`
		}];
	}
}).filter((item) => {
	return item !== undefined;
});

export const getMainContentDisplay = (imageType, imageURL, icon, avatarURL, avatarName) => (
	{
		'displayType': `${imageType}`,
		'imageURL': `${imageURL}`,
		'iconName': `${icon}`,
		'avatarURL': `${avatarURL}`,
		'avatarName': `${avatarName}`
	}
);

export const getMainContentSubtitleDisplay = (subtitleImageType, subtitleImageURL, subtitleIcon, subtitleAvatarURL, subtitleAvatarName) => (
	{
		'displayType': `${subtitleImageType}`,
		'imageURL': `${subtitleImageURL}`,
		'iconName': `${subtitleIcon}`,
		'avatarURL': `${subtitleAvatarURL}`,
		'avatarName': `${subtitleAvatarName}`
	}
);

export const validateActions = (actions, model) => {
	return !isEmpty(actions) && !isEmpty(model);
};

export const getOverflowActionMenu = ({ overflowActions, mainActions }) => {

	let mainActionOverflow, overflowActionsTemp = [];

	if (!isEmpty(mainActions)) {
		mainActionOverflow = mainActions.slice(2);

		mainActionOverflow.map(action => overflowActionsTemp.push(action));

	}

	overflowActions = [...overflowActionsTemp, ...overflowActions];
	return overflowActions;
};

export const imageValidation = (e) => (
	e.target.style.display = 'none');

export const getDropdown = (overflowActionMenu, model, componentView) => {
	let displayOverflowActionMenu = overflowActionMenu.map((action) => {
		return {
			...action,
			'id': `${action.assignmentId}`,
			'label': `${action.label}`
		};
	});

	let overFlowActionClassName = 'overflow-action';
	if (componentView == CARD)
		overFlowActionClassName += ' overflow-actions-extra-padding-right-16';
	else
		overFlowActionClassName += ' overflow-actions-extra-padding-left-8';

	return (
		<span className={overFlowActionClassName}>
			<now-dropdown items={displayOverflowActionMenu}
				icon="ellipsis-v-outline" size="md" variant="secondary"
				append-to-payload={{ model: model }}
				tooltipContent={overflowActionsTooltip}
				select="none"
				bare={true}
				hideCaret={true}></now-dropdown>
		</span>
	);

};

export const checkIfTruncationExists = (host) => {

	const getCardElement = get(host, 'shadowRoot.lastChild');
	const buttonShadowRoot = getCardElement.querySelectorAll('.actions-section div > now-button');
	[...buttonShadowRoot].map((button) => {
		let editedStyle = document.createElement('style');
		editedStyle.textContent = style;
		button.shadowRoot.appendChild(editedStyle);
	});
};

export const hideTooltipIfNoTruncation = (event, elem) => {
	const buttonLabel = elem.elm.shadowRoot.querySelector('span');

	if (buttonLabel.scrollWidth <= elem.elm.clientWidth) {
		const getTooltip = elem.elm.shadowRoot.querySelector('now-tooltip');
		getTooltip ? getTooltip.style.display = 'none' : '';
	}
};

export const getSecondInfo = (secondaryInfo, cardHeight, width, isMainActionsValid, isListView) => {
	if (!isListView) {
		if (cardHeight === SMALL && isMainActionsValid) {
			secondaryInfo.splice(0);
		}
		else if (cardHeight === SMALL || (cardHeight === MEDIUM && isMainActionsValid)) {
			secondaryInfo.splice(2);
		}
	}

	if (isListView) {
		if (width >= TWO_LABEL_VALUE_BREAKPOINT && width < THREE_LABEL_VALUE_BREAKPOINT) {
			secondaryInfo.splice(2);
		}
		else if (width >= THREE_LABEL_VALUE_BREAKPOINT && width < FOUR_LABEL_VALUE_BREAKPOINT) {
			secondaryInfo.splice(3);
		}
	}

	return secondaryInfo;
};