import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import rtlBehavior from '@servicenow/behavior-rtl';
import view from './view.js';
import styles from './styles.scss';
import { NOW_BUTTON_CLICKED, NOW_CARD_CLICKED, DECLARATIVE_ACTION, NOW_DROPDOWN_ITEM_CLICKED } from './constants';
import { checkIfTruncationExists } from '../now-card-evam-record/utils';

/**
* Card component that shows information and actions related to a record in a list.
* Can include a click action, two action buttons, and overflow actions.
*
* ```jsx
* <now-card-evam-record 
*     highlightedHeaderLabel={highlightedHeaderLabel} 
*     highlightedHeaderIcon={highlightedHeaderIcon}
*     highlightedHeaderBkgColor={highlightedHeaderBkgColor}
*     textHeaderLabel={textHeaderLabel}
*     titleLabel={titleLabel} 
*     imageType={imageType} 
*     avatarURL={avatarURL} 
*     avatarName={avatarName}
*     subtitle={subtitle}
*     subtitleImageType={subtitleImageType} 
*     subtitleIcon={subtitleIcon}
*     detailLabelOne={detailLabelOne}
*     detailValueOne={detailValueOne}
*     detailLabelTwo={detailLabelTwo}
*     detailValueTwo={detailValueTwo}
*     detailLabelThree={detailLabelThree}
*     detailValueThree={detailValueThree}
*     detailLabelFour={detailLabelFour}
*     detailValueFour={detailValueFour}
*     mainActions={mainActions}
*     overflowActions={overflowActions}
*     clickAction={clickActions}
*     model={model}
* </now-card-evam-record>
* ```
* 
*
* @summary Record-based content available in card and list items. 
* @seismicElement now-card-evam-record
*/

createCustomElement('now-card-evam-record', {
	renderer: { type: snabbdom },
	view,
	properties: {
		/**
		* Defines what string to display in the highlighted value in the top-left corner.
		* @type {string}
		*/
		highlightedHeaderLabel: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines the icon to show in the highlighted value in the top-left corner. The icon only displays if this property has a value.
		* @type {string}
		*/
		highlightedHeaderIcon: {
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines the color of the highlighted value in the top-left corner.
		* @type {string}
		*/
		highlightedHeaderBkgColor: {
			default: 'low',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines the display text located at the top of the card.
		* @type {string}
		*/
		textHeaderLabel: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines the main title of the card.
		* @type {string}
		*/
		titleLabel: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines what type of visual is displayed in the main content section.
		* @type {string}
		*/
		imageType: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines the image URL displayed for the main content section.
		* @type {string}
		*/
		imageURL: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines the icon displayed in the main content section.
		* @type {string}
		*/
		icon: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines the avatar URL displayed in the main content section.
		* @type {string}
		*/
		avatarURL: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines the avatar name displayed in the main content section.
		* @type {string}
		*/
		avatarName: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines the subtitle of the card.
		* @type {string}
		*/
		subtitle: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines what type of visual is displayed in the subtitle section.
		* @type {string}
		*/
		subtitleImageType: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines the image URL displayed in the subtitle section.
		* @type {string}
		*/
		subtitleImageURL: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines the icon displayed in the subtitle section.
		* @type {string}
		*/
		subtitleIcon: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines the avatar URL displayed in the subtitle section.
		* @type {string}
		*/
		subtitleAvatarURL: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines the avatar name displayed for the subtitle section.
		* @type {string}
		*/
		subtitleAvatarName: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines a key displayed in the secondary information section.
		* @type {string}
		*/
		detailLabelOne: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines a value displayed in the secondary information section.
		* @type {string}
		*/
		detailValueOne: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines a key displayed in the secondary information section.
		* @type {string}
		*/
		detailLabelTwo: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines a value displayed in the secondary information section.
		* @type {string}
		*/
		detailValueTwo: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines a key displayed in the secondary information section.
		* @type {string}
		*/
		detailLabelThree: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines a value displayed in the secondary information section.
		* @type {string}
		*/
		detailValueThree: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines a key displayed in the secondary information section.
		* @type {string}
		*/
		detailLabelFour: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines a value displayed in the secondary information section.
		* @type {string}
		*/
		detailValueFour: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Contains main inline actions of the card (primary/secondary).
		* @type {mainActions[]}
		*/
		mainActions: {
			default: [],
			schema: {
				type: 'array'
			}
		},
		/**
		* Contains other actions present in the dropdown panel of the card.
		* @type {overflowActions[]}
		*/
		overflowActions: {
			default: [],
			schema: {
				type: 'array'
			}
		},
		/**
		* Defines the action metadata when the card is clicked.
		* @type {Object}
		*/
		clickAction: {
			default: {},
			schema: {
				type: 'object'
			}
		},
		/**
		* Placeholder property defining the declarative action model provided by the EVAM infrastructure.
		* @type {Object}
		*/
		model: {
			default: {},
			schema: {
				type: 'object'
			}
		},
		/**
		* Adds an aria-label to the card component.
		* @type {String}
		*/
		cardLabel: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Adds an aria-label that describes the click interaction of the card component.
		* @type {String}
		*/
		buttonLabel: {
			default: '',
			schema: {
				type: 'string'
			}
		}
	},
	initialState: {
		hostElement: null
	},
	actionHandlers: {
		[actionTypes.COMPONENT_RENDERED]: ({ host, updateState }) => {
			updateState({ hostElement: host });
			checkIfTruncationExists(host);
		},
		[NOW_BUTTON_CLICKED]: ({ dispatch, action: { payload: { action, model } } }) => {
			dispatch(DECLARATIVE_ACTION, { action, model });
		},
		[NOW_DROPDOWN_ITEM_CLICKED]: ({ dispatch, action }) => {
			dispatch(DECLARATIVE_ACTION, { action: action.payload.item, model: action.payload.model });
		},
		[NOW_CARD_CLICKED]: ({ dispatch, action: { payload: { action, model } } }) => {
			dispatch(DECLARATIVE_ACTION, { action, model });
		}
	},
	dispatches: {
		/**
		* @private
		*/
		'DECLARATIVE_ACTION': {}
	},
	behaviors: [{ behavior: rtlBehavior }],
	styles
});
