import '@servicenow/now-highlighted-value';
import '@servicenow/now-label-value';
import '@servicenow/now-button';
import '@servicenow/now-card';
import '@servicenow/now-avatar';
import '@servicenow/now-dropdown';
import get from 'lodash/get';
import { LARGE, MEDIUM, SMALL, AVATAR, ICON, IMAGE, CARD, LIST, MEDIUM_CARD, LARGE_CARD, TOGGLE_BREAKPOINT } from './constants';
import {
	getSecondaryInformation, getMainContentDisplay, getMainContentSubtitleDisplay, imageValidation,
	getOverflowActionMenu, validateActions, getDropdown, getSecondInfo, hideTooltipIfNoTruncation
} from './utils';
import { t } from 'sn-translate';
import isEmpty from 'lodash/isEmpty';


const getDisplayElement = (getType, getPosition, componentView) => {
	let avatarSize = SMALL, imageSize = 'sm-image', iconSize = LARGE;
	if (getPosition === 'mainContentDisplay') {
		avatarSize = (componentView === CARD) ? LARGE : MEDIUM;
		imageSize = (componentView === CARD) ? 'card-image' : 'list-image';
		iconSize = 'xl';
	}

	switch (getType.displayType) {
	case IMAGE:
		if (getType.imageURL && getType.imageURL.length > 0) {
			if ((getType.imageURL).includes('.iix'))
				return <img on-error={imageValidation} alt={IMAGE} src={getType.imageURL + '?t=small'} className={imageSize} />;
			else
				return <img on-error={imageValidation} alt={IMAGE} src={getType.imageURL} className={imageSize} />;
		}
		break;
	case ICON:
		return <now-icon icon={getType.iconName} size={iconSize}></now-icon>;
	case AVATAR:
		if ((getType.avatarURL).includes('.iix'))
			return <now-avatar aria-hidden="true" image-src={getType.avatarURL + '?t=small'} size={avatarSize} user-name={getType.avatarName}></now-avatar>;
		else
			return <now-avatar aria-hidden="true" image-src={getType.avatarURL} size={avatarSize} user-name={getType.avatarName}></now-avatar>;
	default:
		break;
	}
};

const CONTENT_DISPLAY = ({ mainContentDisplay, componentView }) => getDisplayElement(mainContentDisplay, 'mainContentDisplay', componentView);

const HEADER_SECTION = ({ highlightedHeaderLabel, highlightedHeaderIcon, textHeaderLabel, highlightedHeaderBkgColor, isMainActionsValid, isOverflowActionsValid, overflowActionMenu, model, componentView }, isListView) => {
	const displayOverflowActions = isMainActionsValid || isOverflowActionsValid;
	const isPresent = isEmpty(textHeaderLabel) && isEmpty(highlightedHeaderLabel) ? '-empty' : '-full';

	return (
		<div className={`header-section ${isPresent} ${componentView}`}>
			
			{highlightedHeaderLabel &&
				<now-highlighted-value label={highlightedHeaderLabel} icon={highlightedHeaderIcon || ''} color={highlightedHeaderBkgColor}
					variant="tertiary"></now-highlighted-value>
			}
			{textHeaderLabel && <now-label-value-inline label={textHeaderLabel} truncated></now-label-value-inline>}
			
			{(displayOverflowActions && !isListView && !isEmpty(overflowActionMenu)) ? getDropdown(overflowActionMenu, model, componentView) : ''}
		</div>
	);
};

const MAIN_CONTENT = (props, isListView) => {
	const { titleLabel, mainContentSubtitleDisplay, subtitle, componentView, secondaryInfo } = props;
	const isCard = componentView === CARD;
	const lines = isListView ? 1 : 2;
	const emptyLabelValue = !secondaryInfo.length ? 'emptyLabelValue' : '';

	return (
		<div className={`main-content ${componentView} ${emptyLabelValue}`}>
			{isCard ? <div className="main-content-display">{CONTENT_DISPLAY(props)}</div> : ''}
			<div>
				<now-card-header heading={{ label: titleLabel, size: MEDIUM, lines: lines, variant: 'primary' }} />
				<div className="main-content-subtitle">
					{getDisplayElement(mainContentSubtitleDisplay, 'mainContentSubtitleDisplay', componentView)}
					<label className="subtitle">{subtitle}</label>
				</div>
			</div>
		</div>
	);
};

const LABEL_VALUE_SECTION = ({ secondaryInfo, componentView, cardHeight, width, isMainActionsValid }, isListView) => {

	const secondInfo = getSecondInfo(secondaryInfo, cardHeight, width, isMainActionsValid, isListView);
	const style = isListView ? { maxWidth: `${100 / secondInfo.length}%` } : '';

	return (
		<div className={'label-value-section ' + componentView}>
			{secondInfo.map((value, index) => {
				return (
					<div style={style}>
						<now-label-value-stacked align="horizontal-equal" delimiter="," items={secondInfo[index]} size="sm" truncated={true}></now-label-value-stacked>
					</div>
				);
			})}
		</div>
	);
};


const ACTION_SECTION = ({ isMainActionsValid, isOverflowActionsValid, mainActions, model, overflowActionMenu, componentView, secondaryInfo }, isListView) => {
	const style = !secondaryInfo.length ? { 'margin-top': '0' } : '';
	const displayOverflowActions = isMainActionsValid || isOverflowActionsValid;
	const noOfActions = mainActions.length > 1 ? 'multiple' : 'single';
	const overFlowActions = (displayOverflowActions && isListView && !isEmpty(overflowActionMenu)) ? getDropdown(overflowActionMenu, model, componentView) : '';
	const extraPadding = (!isListView || overFlowActions) ? '' : 'overflow-actions-extra-padding-32';

	return (
		<div className={`actions-section ${componentView} ${noOfActions} ${extraPadding}`}>
			<div>
				{isMainActionsValid ?
					mainActions.map((action, index) => {
						if (index < 2) {
							return (
								<now-button
									style={style}
									label={action.label}
									icon={action.icon}
									tooltipContent={action.label}
									variant={action.buttonType}
									size="sm"
									append-to-payload={{ action: action, model: model }}
									className={`${action.buttonType} ${componentView}`}
									on-mouseover={hideTooltipIfNoTruncation}>
								</now-button>
							);
						}
					}) : ''}
			</div>
			{overFlowActions}
		</div >
	);
};

const CardEvamRecord = (props) => {
	const componentView = props.componentView;
	const isListView = props.componentView === LIST;
	const isActionsValid = props.isActionsValid;

	const headerSection = HEADER_SECTION(props, isListView);
	const mainContentSection = MAIN_CONTENT(props, isListView);
	const labelValueSection = LABEL_VALUE_SECTION(props, isListView);
	const contentDisplay = CONTENT_DISPLAY(props);
	const actionSection = ACTION_SECTION(props, isListView);

	let interaction = 'none', payload = '';
	if (isActionsValid) {
		interaction = 'click';
		payload = { action: props.clickAction, model: props.model };
	}
	const cardLabel = !isEmpty(props.cardLabel) ? props.cardLabel : t('Card: ') + props.titleLabel;

	return (
		<now-card size={MEDIUM} className={'now-card-template ' + componentView} interaction={interaction} append-to-payload={payload} aria-label={cardLabel} config-aria={{ 'aria-label': props.buttonLabel }}>
			<div className={'now-card-container ' + componentView}>
				{isListView ? <div className="main-content-display">{contentDisplay}</div> : ''}
				<div className="header-content-section">
					<div className="header-content-label">
						{headerSection}
						{mainContentSection}
						{isListView ? labelValueSection : ''}
					</div>
					{isListView ? actionSection : ''}
				</div>
				{!isListView ? labelValueSection : ''}
				{(!isListView && !isEmpty(props.mainActions)) ? actionSection : ''}
			</div>
		</now-card>
	);
};

export default (state) => {
	const { imageType, imageURL, icon, avatarURL,
		avatarName, subtitleImageType, subtitleImageURL, subtitleIcon,
		subtitleAvatarURL, subtitleAvatarName, detailLabelOne, detailValueOne, detailLabelTwo, detailValueTwo,
		detailLabelThree, detailValueThree, detailLabelFour, detailValueFour } = state.properties;

	let { mainActions, model, clickAction } = state.properties;
	const { overflowActions } = state.properties;

	let secondaryInfo = [{ detailLabelOne, detailValueOne },
		{ detailLabelTwo, detailValueTwo },
		{ detailLabelThree, detailValueThree },
		{ detailLabelFour, detailValueFour }];

	const width = get(state.hostElement, 'parentElement.offsetWidth');
	const height = get(state.hostElement, 'parentElement.offsetHeight');
	const cardHeight = (height >= LARGE_CARD) ? LARGE : (height >= MEDIUM_CARD) ? MEDIUM : SMALL;
	let componentView = LIST;
	if (width <= TOGGLE_BREAKPOINT) {
		componentView = CARD;
	}

	const props = {
		mainContentDisplay: getMainContentDisplay(imageType, imageURL, icon, avatarURL, avatarName),
		mainContentSubtitleDisplay: getMainContentSubtitleDisplay(subtitleImageType, subtitleImageURL, subtitleIcon, subtitleAvatarURL, subtitleAvatarName),
		secondaryInfo: getSecondaryInformation(secondaryInfo),
		overflowActionMenu: getOverflowActionMenu(state.properties, state.componentView),
		isMainActionsValid: validateActions(mainActions, model),
		isOverflowActionsValid: validateActions(overflowActions, model),
		isActionsValid: validateActions(clickAction, model),
		componentView: componentView,
		cardHeight: cardHeight,
		width: width,
		...state.properties
	};

	return (<CardEvamRecord {...props} />);
};

